const LeftMenu = ({ leftMenu, onTemplateClick }) => {
  const [search, setSearch] = useState("");
  const filterGroups = (groups) => {
    return groups.map(group => ({
      ...group,
      items: group.items.filter(item =>
        item.label.toLowerCase().includes(search.toLowerCase())
      )
    })).filter(group => group.items.length > 0);
  };

  return (
    <div className="left-menu">
      <input
        className="menu-search"
        type="text"
        placeholder=" ..."
        title="   "
        value={search}
        onChange={e => setSearch(e.target.value)}
      />
      {Object.keys(leftMenu).map(categoryKey => {
        const groups = filterGroups(leftMenu[categoryKey]);
        if (!groups.length) return null;
        return (
          <div key={categoryKey}>
            <h3 className="category-title">
              {categoryKey.charAt(0).toUpperCase() + categoryKey.slice(1)}
            </h3>
            {groups.map((group, idx) => (
              <div key={idx} className="menu-group">
                <div className="menu-group-header" title=",  / ">
                  <span className="menu-group-title">{group.groupTitle}</span>
                </div>
                <div className="menu-group-items">
                  {group.items.map((item, index) =>
                    item.type === "template" ? (
                      <TemplateBlock key={`${categoryKey}-${idx}-${index}`} template={item} onTemplateClick={onTemplateClick} />
                    ) : (
                      allowedInitialTriggerTypes.includes(item.type) && (
                        <SidebarBlock key={`${categoryKey}-${idx}-${index}`} type={item.type} label={item.label} />
                      )
                    )
                  )}
                </div>
              </div>
            ))}
          </div>
        );
      })}
    </div>
  );
};

const DroppedBlock = ({ block, blocks, onSelectBlock, onAddNext, onDeleteBlock, updateBlockPosition }) => {
  const [{ isDragging }, drag] = useDrag({
    type: ItemTypes.BLOCK,
    item: block,
    collect: monitor => ({ isDragging: monitor.isDragging() })
  });

  const commonStyle = {
    opacity: isDragging ? 0.5 : 1,
    transition: "all 0.2s"
  };

  const absPos = computeAbsolutePosition(block, blocks);
  const style = block.groupId
    ? { ...commonStyle, marginLeft: block.position.x, marginTop: block.position.y, position: "relative" }
    : { ...commonStyle, left: absPos.x, top: absPos.y, position: "absolute" };

  if (block.type === "group") {
    const groupChildrenRef = useRef(null);
    const [, dropGroup] = useDrop({
      accept: ItemTypes.BLOCK,
      drop: (item, monitor) => {
        if (item.groupId === block.id) {
          const clientOffset = monitor.getClientOffset();
          const groupRect = groupChildrenRef.current.getBoundingClientRect();
          const newRelPos = {
            x: clientOffset.x - groupRect.left,
            y: clientOffset.y - groupRect.top
          };
          updateBlockPosition(item.id, newRelPos);
        }
      }
    });
    return (
      <div
        ref={drag}
        id={`block-${block.id}`}
        className="dropped-block group-block"
        style={style}
        title={`: ${block.label}`}
        onClick={e => { e.stopPropagation(); onSelectBlock(block); }}
      >
        <div className="group-header" style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
          <span>{block.label}</span>
          <button
            className="delete-button"
            title=" "
            onClick={e => { e.stopPropagation(); onDeleteBlock(block.id); }}
          >
            &times;
          </button>
        </div>
        <div ref={dropGroup} className="group-children">
          <div className="group-triggers-header">:</div>
          <div className="group-triggers">
            {blocks.filter(child => child.groupId === block.id).map(child => (
              <DroppedBlock
                key={child.id}
                block={child}
                blocks={blocks}
                onSelectBlock={onSelectBlock}
                onAddNext={onAddNext}
                onDeleteBlock={onDeleteBlock}
                updateBlockPosition={updateBlockPosition}
              />
            ))}
          </div>
          {blocks.filter(child => child.groupId === block.id).length === 0 && (
            <div
              className="group-placeholder"
              onClick={e => { e.stopPropagation(); onAddNext(block); }}
              title=",   "
            >
              +  
            </div>
          )}
        </div>
        {blocks.filter(child => child.groupId === block.id).length > 0 && (
          <button
            className="add-next-button"
            title="   "
            onClick={e => { e.stopPropagation(); onAddNext(block); }}
          >
            +
          </button>
        )}
      </div>
    );
  }

  return (
    <div
      ref={drag}
      id={`block-${block.id}`}
      className="dropped-block"
      style={style}
      title={`: ${block.label}`}
      onClick={e => { e.stopPropagation(); onSelectBlock(block); }}
    >
      <span className="block-icon">{emojiMapping[block.type] || ""}</span>
      <span className="block-label">{block.label}</span>
      <button
        className="delete-button"
        title=" "
        onClick={e => { e.stopPropagation(); onDeleteBlock(block.id); }}
      >
        &times;
      </button>
      <button
        className="add-next-button"
        title="  "
        onClick={e => { e.stopPropagation(); onAddNext(block); }}
      >
        +
      </button>
    </div>
  );
};

const Canvas = ({ blocks, addBlock, updateBlockPosition, onSelectBlock, onAddNext, onDeleteBlock }) => {
  const canvasRef = useRef(null);
  const [, drop] = useDrop({
    accept: ItemTypes.BLOCK,
    drop: (item, monitor) => {
      const clientOffset = monitor.getClientOffset();
      const canvasRect = canvasRef.current.getBoundingClientRect();
      const dropPosition = {
        x: clientOffset.x - canvasRect.left,
        y: clientOffset.y - canvasRect.top
      };

      if (item.fromSidebar && TRIGGER_TYPES.includes(item.type)) {
        const group = getOrCreateGroupForTrigger(dropPosition, blocks, addBlock, item);
        const newTrigger = {
          type: item.type,
          label: item.label,
          position: getNextChildPosition(group, blocks),
          id: generateId(),
          groupId: group.id,
          next: "",
          ...getDefaultBlockProps(item.type)
        };
        addBlock(newTrigger);
        console.log(`  "${newTrigger.label}"   "${group.label}"`);
        return;
      }

      if (!item.id) {
        const newBlock = {
          ...item,
          position: dropPosition,
          id: generateId(),
          next: "",
          ...getDefaultBlockProps(item.type)
        };
        addBlock(newBlock);
      } else {
        if (item.groupId) {
          const parent = blocks.find(b => b.id === item.groupId);
          if (parent) {
            const relativePos = {
              x: dropPosition.x - parent.position.x,
              y: dropPosition.y - parent.position.y
            };
            updateBlockPosition(item.id, relativePos);
          }
        } else {
          updateBlockPosition(item.id, dropPosition);
        }
      }
    }
  });

  const absPositions = blocks.map(b => computeAbsolutePosition(b, blocks));
  const maxX = absPositions.length ? Math.max(...absPositions.map(p => p.x), 800) : 800;
  const maxY = absPositions.length ? Math.max(...absPositions.map(p => p.y), 600) : 600;
  const canvasStyle = { width: maxX + 300, height: maxY + 200 };

  useEffect(() => { window.dispatchEvent(new Event('resize')); }, [blocks]);

  const topLevelBlocks = blocks.filter(b => !b.groupId);
  return (
    <div id="canvas" ref={node => { drop(node); canvasRef.current = node; }} className="canvas" style={canvasStyle} title=" ">
      {topLevelBlocks.map(block => (
        <DroppedBlock
          key={block.id}
          block={block}
          blocks={blocks}
          onSelectBlock={onSelectBlock}
          onAddNext={onAddNext}
          onDeleteBlock={onDeleteBlock}
          updateBlockPosition={updateBlockPosition}
        />
      ))}
      {blocks.map(block => {
        if (block.type === "route") {
          return (
            <React.Fragment key={`xarrow-${block.id}`}>
              {block.nextTrue && blocks.find(b => b.id === block.nextTrue) && (
                <Xarrow key={`xarrow-${block.id}-true`} start={`block-${block.id}`} end={`block-${block.nextTrue}`} color="green" strokeWidth={2} animateDrawing={false} />
              )}
              {block.nextFalse && blocks.find(b => b.id === block.nextFalse) && (
                <Xarrow key={`xarrow-${block.id}-false`} start={`block-${block.id}`} end={`block-${block.nextFalse}`} color="red" strokeWidth={2} animateDrawing={false} />
              )}
            </React.Fragment>
          );
        } else if (block.next && blocks.find(b => b.id === block.next)) {
          return (
            <Xarrow key={`xarrow-${block.id}`} start={`block-${block.id}`} end={`block-${block.next}`} color="#1e88e5" strokeWidth={2} animateDrawing={false} />
          );
        }
        return null;
      })}
    </div>
  );
};

